import { nodeName } from 'jquery';
//组件名首字母一定要大写
import React, { Component } from 'react'

export class Control extends Component {
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[
                {title:'1',done:true},//true为未完成
                {title:'2',done:true},//false为已完成
                {title:'3',done:false},
                {title:'4',done:false}
            ]
        }
    }
    handleChange=(e)=>{
        this.setState({inpValue:e.target.value});
    }
    componentDidMount(){
       this.input.focus();//默认焦点
       let todos = localStorage.getItem('todos');
       if(todos){
           this.setState({
               todos:JSON.parse(todos)
           })
       }
    }
    componentDidUpdate(preProps,preState){
        if(preState.todos!==this.state.todos){
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        }   
    }
    addTodo=()=>{//添加函数
        // this.state.todos.push({
        //     title:this.state.inpValue
        // })
        if(this.state.inpValue==""){
            return;
        }
        this.setState({
            todos:[
                {
                    title:this.state.inpValue,
                    done:false
                },
                ...this.state.todos
            ]               
        })
        this.state.inpValue='';
    }
    deleteTodo=(idx)=>{//删除函数法一
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));//深拷贝一份
        newTodos.splice(idx,1);//第一个参数为从第几个删，第二个参数为删多少个
        this.setState({
            todos:this.state.todos.filter((item,index)=> index != idx)
        });
    }
    // deleteTodo=(idx)=>{//删除函数法二
    //     // setState 是异步执行的，注意callback的用法
    //     // this.setState({
    //     //     todos: this.state.todos.filter((item,index)=>index!=idx)
    //     // },()=>{
    //     //     localStorage.setItem(
    //     //         'todos',
    //     //         JSON.stringify(this.state.todos)
    //     //     )
    //     // });
    // }
    toggle=(index)=>{//点击√到完成那一块
        let newTodos = JSON.parse(JSON.stringify(this.state.todos));
        newTodos[index].done=!newTodos[index].done;
        this.setState({
            todos:newTodos
        })
    }
    renderTodos=(done)=>{
        return this.state.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <li>
                    <input 
                        onClick={()=>this.toggle(idx)}
                        checked={done} 
                        type="checkbox"
                    />
                    <span dangerouslySetInnerHTML={{__html:todo.title}}></span>
                    <button onClick={()=>this.deleteTodo(idx)}>删除</button>
                </li>
            }        
        })
    }
    render() {
        const{todos,inpValue}=this.state;
        let arr1=todos.filter(item=>!item.done);//正在进行的个数
        let arr2=todos.filter(item=>item.done);//已经完成的个数
        return (
            <div className="box">
                <label htmlFor="inp">todolist</label>
                <input 
                    ip='inp'
                    ref={inp=>this.input=inp} 
                    type='text' 
                    value={inpValue} 
                    onChange={this.handleChange}
                />
                <button onClick= {this.addTodo}>添加</button>
                <h2>正在进行{arr1.length}</h2>
                <ul>
                    {this.renderTodos(false)}
                </ul>
                <h2>已经完成{arr2.length}</h2>
                <ul>
                    {this.renderTodos(true)}
                </ul>
                {/* 条件渲染 */}
                {
                    <button>登录</button>
                }
                <button>{2>3?'登录':'注册'}</button>
            </div>
        )
    }
}

export default Control;


