import React from 'react'
import './Carousel.css';
import { TabBar } from 'antd-mobile';
const Picture = (props)=>{
        return(
            <div className="background">
                {/* 商品背景图和返回键 */}
                <div className='return'
                    onClick={()=>{props.history.goBack()}}
                ><i className='iconfont icon-fanhui'></i></div>
                {/* 底部按钮 */}
                <div className="bottom">
                    <div className="btnBox1">
                        <TabBar
                            unselectedTintColor="#949494"
                            tintColor="orange"
                            barTintColor="white"
                        >
                            <TabBar.Item
                                title="店铺"
                                icon={ 
                                    <i className='iconfont icon-shop'></i>
                                }
                                selectedIcon={ 
                                    <i className='iconfont icon-dianpu'></i>
                                }
                            >
                            </TabBar.Item>
                            <TabBar.Item
                                title="客服"
                                icon={ 
                                    <i className='iconfont icon-kefu'></i>
                                }
                                selectedIcon={ 
                                    <i className='iconfont icon-kefu1'></i>
                                }
                            >
                            </TabBar.Item>
                            <TabBar.Item
                                title="收藏"
                                icon={ 
                                    <i className='iconfont icon-shoucang'></i>
                                }
                                selectedIcon={ 
                                    <i className='iconfont icon-favourited'></i>
                                }
                            >
                            </TabBar.Item>
                        </TabBar>
                </div>
                <div className="btnBox2">
                    <button className="btn add">加入购物车</button>
                    <button className="btn buy">立即购买</button>
                </div> 
            </div>
            </div>
        )
}   
export default Picture;