import React from 'react';
import { TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: '/',
        };
    }
    render() {
        const { pathname } = this.props.location;
        console.log(pathname.split("/"));//分割 可以查看路径为几层
        if(pathname.split("/").length > 2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="orange"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={ 
                            <i className='iconfont icon-Homehomepagemenu'></i>
                        }
                        selectedIcon={ 
                            <i className='iconfont icon-Homehomepagemenu1'></i>
                        }
                        selected={ pathname === '/' }//默认是首页  /  记得 在App.js中也得使用  /
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="购物车"
                        key="cart"
                        icon={
                            <i className='iconfont icon-gouwuchekong'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwucheman'></i>
                        } 
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart');
                        } }
                        data-seed="logId1"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="订单列表"
                        key="list"
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon'></i>
                        } 
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list');
                        } }
                    >
			        </TabBar.Item>
                    <TabBar.Item
                        title="我的淘宝"
                        key="my"
                        icon={ 
                            <i className='iconfont icon-wode'></i> 
                        }
                        selectedIcon={ 
                            <i className='iconfont icon-wodedangxuan'></i>
                        } 
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my');
                        } }
                    >
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);//高阶组件